  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.excerpt-2 {
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

/*-----------------------------------------------------------------------------------

	layouts.scss

-----------------------------------------------------------------------------------*/
/* ----------------------------------------------------------------
	Sections
-----------------------------------------------------------------*/
.section {
  position: relative;
  margin: var(--section-margin) 0;
  padding: var(--section-padding) 0;
  background-color: var(--section-bg);
  overflow: hidden;
}
.section .container {
  z-index: 2;
}
.section .container + .video-wrap {
  z-index: 1;
}

.parallax {
  background-color: transparent;
  background-attachment: fixed;
  background-position: 50% 0;
  background-repeat: no-repeat;
  overflow: hidden;
}

.mobile-parallax,
.video-placeholder {
  background-size: cover !important;
  background-attachment: scroll !important;
  background-position: center center;
}

.revealer-image {
  position: relative;
  bottom: -calc(var(--section-padding) * 2);
  transition: bottom 0.3s ease-in-out;
  -webkit-transition: bottom 0.3s ease-in-out;
  -o-transition: bottom 0.3s ease-in-out;
}

.section:hover .revealer-image {
  bottom: -var(--section-padding);
}

/* ----------------------------------------------------------------
	Columns & Grids
-----------------------------------------------------------------*/
.postcontent {
  width: var(--postcontent);
}
.postcontent.bothsidebar {
  width: var(--bothsidebar);
}

.sidebar {
  width: var(--sidebar);
}

.col_full {
  width: 100%;
}

.col_half {
  width: 48%;
}

.col_one_third {
  width: 30.63%;
}

.col_two_third {
  width: 65.33%;
}

.col_one_fourth {
  width: 22%;
}

.col_three_fourth {
  width: 74%;
}

.col_one_fifth {
  width: 16.8%;
}

.col_two_fifth {
  width: 37.6%;
}

.col_three_fifth {
  width: 58.4%;
}

.col_four_fifth {
  width: 79.2%;
}

.col_one_sixth {
  width: 13.33%;
}

.col_five_sixth {
  width: 82.67%;
}

body.stretched .container-fullwidth {
  position: relative;
  padding: 0 60px;
  width: 100%;
}

.postcontent,
.sidebar,
.col_full,
.col_half,
.col_one_third,
.col_two_third,
.col_three_fourth,
.col_one_fourth,
.col_one_fifth,
.col_two_fifth,
.col_three_fifth,
.col_four_fifth,
.col_one_sixth,
.col_five_sixth {
