/*-----------------------------------------------------------------------------------

	topbar.scss

-----------------------------------------------------------------------------------*/
/* ----------------------------------------------------------------
	Top Bar
-----------------------------------------------------------------*/
#top-bar {
  position: relative;
  border-bottom: 1px solid #EEE;
  height: var(--topbar-height);
  line-height: var(--topbar-line-height);
  font-size: 13px;
}
#top-bar .col_half {
  width: auto;
}

/* ----------------------------------------------------------------
	Top Links
-----------------------------------------------------------------*/
.top-links {
  position: relative;
  float: left;
}
.top-links ul {
  margin: 0;
  list-style: none;
}
.top-links ul li {
  float: left;
