}

#header.full-header {
  border-bottom-color: #EEE;
}

body.stretched #header.full-header .container {
  width: 100%;
  max-width: none;
  padding: 0 30px;
}

#header.transparent-header.full-header #header-wrap {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

