}

/* ----------------------------------------------------------------
	Primary Menu
-----------------------------------------------------------------*/
#primary-menu {
  float: right;
  height: var(--header-height);
  display: flex;
  justify-content: center;
  flex-direction: column;
}

#primary-menu ul.primary-menu-list {
  display: flex;
  align-items: center;
}

#primary-menu ul.mobile-primary-menu {
  display: none;
}

#header.full-header #primary-menu > ul {
  float: left;
  padding-right: 15px;
  margin-right: 15px;
  border-right: 1px solid #EEE;
}

#header.transparent-header.full-header #primary-menu > ul {
  border-right-color: rgba(0, 0, 0, 0.1);
}

#primary-menu-trigger,
#page-submenu-trigger {
  opacity: 0;
  pointer-events: none;
  cursor: pointer;
  font-size: 14px;
  position: absolute;
  top: 50%;
  margin-top: -25px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  -webkit-transition: opacity 0.3s ease;
  -o-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
}

#primary-menu ul {
  list-style: none;
  margin: 0;
}

#primary-menu > ul {
  float: left;
}

#primary-menu ul.primary-menu-list li {
  position: relative;
}

#primary-menu ul.primary-menu-list li.mega-menu {
  position: inherit;
}

#primary-menu ul.primary-menu-list > li {
  float: left;
  margin-left: 2px;
  height: 100%;
  display: flex;
  align-items: center;
}

#primary-menu ul.primary-menu-list > li.sub-menu a {
  padding-right: 25px;
  position: relative;
}
#primary-menu ul.primary-menu-list > li.sub-menu a:after {
  content: "";
  background-image: url("../images/icons/submenu.png");
  background-position: right center;
  background-repeat: no-repeat;
  position: absolute;
  width: 20px;
  height: 22px;
  right: 0;
  top: 3px;
  transform: rotate(90deg);
}

#primary-menu > ul > li:first-child {
  margin-left: 0;
}

#primary-menu > ul li > a > div {
  position: relative;
}
#primary-menu > ul li > a > div:before {
  content: "";
  position: absolute;
  z-index: -1;
  bottom: -10px;
  left: 0;
  width: 20px;
  height: 3px;
  background: var(--theme-color);
  transform: translateX(20px);
  transform-origin: 50% 100%;
  transition: transform 0.3s, opacity 0.3s;
  transition-timing-function: ease;
  transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);
  opacity: 0;
}
#primary-menu > ul li > a.nav-last-item div:before {
  content: none;
}
#primary-menu > ul li:hover > a > div:before, #primary-menu > ul li:focus > a > div:before, #primary-menu > ul li.current > a > div:before {
  transform: translateX(0);
  transform-origin: 50% 0%;
  opacity: 1;
}

body.device-md #primary-menu > ul li > a > div:before, body.device-sm #primary-menu > ul li > a > div:before, body.device-xs #primary-menu > ul li > a > div:before {
  content: none;
}

#primary-menu ul.primary-menu-list li > a {
  display: block;
  line-height: 22px;
  margin-right: 30px;
  color: var(--primary-menu-font-color);
  font-weight: var(--primary-menu-font-weight);
  font-size: var(--primary-menu-font-size);
  letter-spacing: var(--primary-menu-font-spacing);
  text-transform: var(--primary-menu-font-tt);
  font-family: var(--body-font);
  -webkit-transition: margin 0.4s ease, padding 0.4s ease;
  -o-transition: margin 0.4s ease, padding 0.4s ease;
  transition: margin 0.4s ease, padding 0.4s ease;
}

#primary-menu ul.primary-menu-list li > a.button {
  color: white;
}

#primary-menu ul.primary-menu-list li > a span {
  display: none;
}

#primary-menu ul.primary-menu-list li > a i {
  position: relative;
  top: -1px;
  font-size: 14px;
  width: 16px;
  text-align: center;
  margin-right: 6px;
  vertical-align: top;
}

#primary-menu ul.primary-menu-list li > a i.icon-angle-down:last-child {
  font-size: 12px;
  margin: 0 0 0 5px;
}

body.no-superfish #primary-menu li:hover > ul:not(.mega-menu-column),
body.no-superfish #primary-menu li:hover > .mega-menu-content {
  display: block;
}

#primary-menu ul.primary-menu-list ul:not(.mega-menu-column),
#primary-menu ul.primary-menu-list li .mega-menu-content {
  display: none;
  position: absolute;
  width: var(--primary-menu-submenu-width);
  background-color: #FFF;
  box-shadow: 0px 13px 42px 11px rgba(0, 0, 0, 0.05);
  border: 1px solid #EEE;
  border-top: 2px solid var(--theme-color);
  height: auto;
  z-index: 199;
  top: 40px;
  left: 0;
  margin: 0;
}

#primary-menu ul.primary-menu-list li .mega-menu-content {
  display: -ms-flex;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -ms-flex-align: stretch;
  align-items: stretch;
}

#primary-menu.on-click ul li .mega-menu-content {
  display: none;
}

#primary-menu.on-click ul li .mega-menu-content.d-block {
  display: -ms-flex !important;
  display: flex !important;
}

#primary-menu ul ul:not(.mega-menu-column) ul {
  top: -2px !important;
  left: 100% !important;
}

#primary-menu ul.primary-menu-list ul.menu-pos-invert:not(.mega-menu-column),
#primary-menu ul.primary-menu-list li .mega-menu-content.menu-pos-invert {
  left: auto;
  right: 0;
}

#primary-menu ul.primary-menu-list ul:not(.mega-menu-column) ul.menu-pos-invert {
  right: calc(var(--primary-menu-submenu-width) - 2);
}

#primary-menu ul.primary-menu-list ul li {
  float: none;
  margin: 0;
}

#primary-menu ul.primary-menu-list ul li:first-child {
  border-top: 0;
}

#primary-menu ul.primary-menu-list ul li > a {
  font-size: var(--primary-menu-submenu-font-size);
  font-weight: var(--primary-menu-submenu-font-weight);
  color: #666;
  padding-top: 10px;
  padding-bottom: 10px;
  border: 0;
  letter-spacing: 0;
  font-family: var(--primary-menu-submenu-font);
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  margin-right: 0;
  padding-left: 10px;
  padding-right: 30px;
}
#primary-menu ul.primary-menu-list ul li > a:after {
  content: none !important;
}
#primary-menu ul.primary-menu-list ul li > a div:before {
  content: none;
}

#primary-menu ul.primary-menu-list ul li > a i {
  vertical-align: middle;
}

#primary-menu ul.primary-menu-list ul li:hover > a, #primary-menu ul.primary-menu-list ul li.current > a {
