  display: inline-block;
  vertical-align: top;
  margin: 0.3em 0.4em 0.3em 0;
}

.jssocials-share:last-child {
  margin-right: 0;
}

.jssocials-share-logo {
  width: 1em;
  vertical-align: middle;
